*,
* ::before,
* ::after {
  box-sizing: border-box;
}

.page {
  height: 100%;
}

.page__body {
  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100%;

  font-family: "Lato", sans-serif;
  font-size: 14px;
  line-height: 18px;

  color: #000000;
  background-color: #ffffff;

  @media (min-width: $desktop-width) {
    max-width: 1920px;
    margin: 0 auto;
  }
}
