.catalog {
  margin: 0;
  padding: 0;
  list-style-type: none;

  border-top: 1px solid #ebebeb;
  border-bottom: 1px solid #ebebeb;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 60px;

    border: none;
    margin-top: 84px;
    margin-bottom: 70px;
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: repeat(4, 1fr);
    column-gap: 80px;
    row-gap: 63px;

    margin-bottom: 75px;
  }
}

.catalog__item {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  row-gap: 15px;

  padding: 20px 20px 25px 20px;
  border-bottom: 1px solid #ebebeb;

  @media (min-width: $tablet-width) {
    display: block;

    padding: 0 37px 40px;
    background: linear-gradient(0deg, #f2f2f2 90%, #ffffff 90%);
    border: none;
  }
}

.catalog__item:last-child {
  display: block;

  @media (min-width: $tablet-width) {
    margin-top: 5%;
    background: none;
    border: 2px solid #f2f2f2;
  }
}

.catalog__image-link {
  display: block;
  grid-column: 1 / 2;
  grid-row: 1 / 3;

  color: #444444;
  text-decoration: none;

  margin: 0 auto;
}

.catalog__title {
  display: block;
  grid-column: 2 / 3;
  grid-row: 1 / 2;

  font-family: "Oswald", sans-serif;
  color: #222222;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  text-decoration: none;

  margin-bottom: 14px;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 24px;
    text-align: center;

    margin: 0 auto 20px auto;
    max-width: 140px;
  }

  @media (min-width: $desktop-width) {
    max-width: 140px;
  }
}

.catalog__info {
  grid-column: 2 / 3;
  grid-row: 2 / 3;

  color: #444444;
  font-size: 12px;
  line-height: 16px;

  margin-bottom: 21px;

  @media (min-width: $tablet-width) {
    font-size: 16px;
    line-height: 20px;
    border-collapse: collapse;
    width: 100%;
  }
}

.catalog__row {
  @media (min-width: $tablet-width) {
    border-bottom: 1px solid #cdcdcd;
  }
}

.catalog__description {
  min-width: 70px;

  padding: 0;

  @media (min-width: $tablet-width) {
    padding: 5px 0;
  }
}

.catalog__number {
  min-width: 70px;
  text-align: right;

  padding: 0;

  @media (min-width: $tablet-width) {
    padding: 5px 0;
  }
}

.catalog__button {
  grid-column: 1 / 3;
}
